.app{
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.bkg{
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(16px);
    background-color: #00000070;
}

.container{
    width: 70vw;
    height: 70vh;
    margin: 15vh 15vw;
    top: 0;
    overflow: hidden;
    display: grid;
    border-radius: 1.5rem;
    grid-template-columns: 1fr;
    grid-template-rows: 50vh 20vh;
}

.citiesContainer{
    display: flex;
    height: 20vh;
    background-color: rgba(255, 244, 213, 0.479);
    backdrop-filter: blur(16px);
    justify-content: space-between;
}

.app,
.container{
    background-image: url('./assets/bkg2.jpg');
    background-repeat: no-repeat;
    background-size: cover; 
    position:absolute;

}